<template>
  <!-- -->
  <div class="add_vehicle">
    <!--  -->
    <div class="add_vehicle_top">
      	<p></p>
        <div class="add_top_add">
            <mt-button type="primary" size="small"></mt-button>
            <mt-button type="default" size="small" @click="phone"></mt-button>
        </div>
    </div>
     <!--  -->
    <div class="add_vehicle_vont">
      <div class="add_vehicle_firstvont">
        <div class="car_number">
          <span></span>
          <div class="car_number1">
            <select v-model="plate">
              <option value=""></option>
            </select>
            <input placeholder="" type="number" v-model="cnub"/>
          </div>
           <!-- <mt-field label="" placeholder="" type="number"></mt-field> -->
        </div>
        <p>
          <mt-field label="" placeholder="6" type="number" v-model="bodynub"></mt-field>
        </p>
        <p>
           <mt-field label="" placeholder="6" type="number" v-model="engnub"></mt-field>
        </p>
       <p>
          <mt-field label="" placeholder="" type="text" v-model="cars"></mt-field>
       </p>
        <p>
          <mt-field label="" placeholder="" type="date" v-model="time"></mt-field>
        </p>
        <p>
           <mt-field label="" placeholder="" type="number" v-model="km">km</mt-field>
        </p>


      </div>
      <div class="add_vehicle_secondvont">
       <!-- <p class="add_vehicle_switch" >
            <span></span>
            <mt-switch  v-model="item.accept"></mt-switch>
        </p> -->
        <div class="gengduo">
            <p></p><i class="iconfont icon-youjiantou"></i>
        </div>
       <div class="car_number">
         <span></span>
         <div class="car_number2">
           <select v-model="carsdate">
             <option value="1">1</option>
           </select>
         </div>
       </div>
        <p>
            <mt-field label="" placeholder="" type="date" v-model="oldtime"></mt-field>
        </p>
        <p>
            <mt-field label="" placeholder="" type="date" v-model="newtime"></mt-field>
        </p>
        <p>
            <mt-field label="" placeholder="" type="date" v-model="cartime"></mt-field>
        </p>
        <p>
            <mt-field label="" placeholder="(/)" type="text" v-model="illega"></mt-field>
        </p>

        <div class="check_btn">
          <mt-button type="default" size="small" class="check" @click="query"></mt-button>
          <mt-actionsheet :actions="actions" v-model="sheetVisible"></mt-actionsheet>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'add_vehicle',
  data () {
    return {
      //
      msg: 'add',
      plate:'',  //
      cnub:'', //
      bodynub:'', //
      engnub:'', //
      phone_nub:'', //
      accept:false,
      cars:'', //
      time:'', //
      km:'' ,//
      carsdate:'' ,//
      oldtime:'' ,//
      newtime:'' ,//
      cartime:'', //
      illega:'' ,//
      // action sheet 
      actions: [{
        name: '',
        method : this.getCamera	// methods
      }, {
        name: '',
        method : this.getLibrary	// methods
      }],
      // action sheet falsesheetVisible
      sheetVisible: false
      }

  },
  mounted:function(){
        this.$store.state.heard_title =' - '
  },
  methods:{
    openPicker() {
            this.$refs.picker.open();
          },
    phone:function(){

       this.sheetVisible = true;
    },
    getCamera: function(){
       console.log("")
     },
    getLibrary: function(){
        console.log("")
    },
    //
     query:function(){
       var  that = this;
     layui.use('layer', function(){
         var layer = layui.layer;
       if(that.cnub.length!=6){
         layer.msg("!");
       }else if(that.plate = ''){
         layer.msg("")
       }else if(that.cnub = ''){
         layer.msg("")
       }else if(that.bodynub = ''){
         layer.msg("")
       }else if(that.engnub = ''){
         layer.msg("")
       }else if(that.phone_nub = ''){
         layer.msg("")
       }else {
           that.$addr.post('/index/vehicle/add',{
             number:that.plate+that.cnub,
             imei: that.bodynub,
             motor: that.engnub,
             type_id: that.cars,
             registertime: that.time,
              km: that.km,
              digit: that.carsdate,
              booktime:that.oldtime,
              insurancetime:that.newtime,
              licensetime:that.cartime,
              illega:that.illega
           })
                  .then(response => {
           
                    if(response.data.code == 200){
                      layui.use('layer', function() {
                        var layer = layui.layer;
                          layer.msg("")
                          that.$router.push({
                            path: '/exclusive_services',
                            });
                        })
                    }
          })
         }
       })
     }
  }
}
</script>
<style scoped>
  @import '../assets/css/add_vehicle.css';

</style>



