<template>
  <div class="examination_all">
  	<div class="examination_top" >
  			<p v-bind:class="{ break: isBreak }" @click="exa_switch_one" v-model="value"></p>
  			<p v-bind:class="{ break: !isBreak }" @click="exa_switch_two" v-model="value"></p>
  	</div>
  	<div class="examination_center">
  		<div class="examination_center_one " v-show="isBreak">
  			<div class="examination_position">
  				<p></p>
            <input type="text" name="" placeholder="" v-model="address" @click="toMap">
  			</div>
        <div class="examination_location"  id="container" >

        </div>
  			<div class="center_one_inp">
  				<div class="examination_inp">
  					<p></p>
  					<input type="datetime-local" name="time" placeholder="" id="" value="" v-model="date"/>
  				</div>
  				<div class="examination_inp">
  					<p></p>
  					<input type="nub" name="phone"  placeholder="" id="" value=""v-model="phone" />
  				</div>
  			</div>
  			<div class="examination_payment">
  				<button type="button"><span >300</span></button>
  			</div>
  		</div>
  		<div class="examination_center_one" v-show="!isBreak" >
  			<div class="center_one_all">
  				<div class="center_one_inp">
  					<div class="examination_inp">
  						<p></p>
  						<input type="date" name="time" placeholder="" id="" value="" v-model="date"/>
  					</div>
  					<div class="examination_inp">
  						<p></p>
  						<input type="nub" name="phone"  placeholder="" id="" value=""v-model="phone" />
  					</div>
  				</div>
  				<div class="center_two">
  					<img src="../../static/img/location.png" >
  					<span></span>
  				</div>
  				<div class="examination_payment">
  					<button type="button"><span >300</span></button>
  				</div>
  			</div>
  		</div>
  		<div class="examination_bottom">
  			<div class="bottom_header">
  			   <span class="line"></span>
  			   <span class="text"></span>
  			   <span class="line"></span>
  			</div>
  			<div class="bottom_text">
  				<p class="text"></p>
  				<p class="text_two">
  				<span>1</span><br />
  				<span>2</span><br />
  				<span>3</span><br />
  				<span>4</span><br />
  				</p>
  				<p class="text"></p>
  				<p class="text_two">
  				<span>1</span><br />
  				<span>2</span><br />
  				<span>3</span><br />
  				<span>4</span><br />
  				<span>5</span><br />
  				<span>6</span><br />
  				<span>7</span><br />
  				</p>
  				<p class="text">0371-55030777</p>
  			</div>
  		</div>
  	</div>
  	<div class="examination_last">
  		<div class="last_left">
  			<img src="../../static/img/customer.png" >
  			<p></p>
  		</div>
  		<div class="last_right">
        <input type="button" @click="appointment" :value="text" :disabled="bled"/>

  		</div>
  	</div>
  </div>
</template>

<script>
  export default {

    name: 'examination',
    data() {
      return {
        value:1,
        city: '',
        isBreak:true,
        address:'',
        date:'',
        phone:'',
        //position:[],
        longitude:'',//
        latitude:'',//
        text:"",
        bled:false
      }
    },
    created() {
       this.getLocation()
    },
    mounted:function(){
      //var  ss = this.$route.query
      var that = this;
        that.$store.state.heard_title =' - ';
       var geolocation = new qq.maps.Geolocation();
       var options = {
           timeout: 600000
       };
       geolocation.getLocation(showPosition, showErr, options);
        function showPosition(position) {
          //
          if(that.latitude == ''){
            that.latitude = position.lat;
            that.longitude = position.lng;
            that.address = position.addr;
            var center = new TMap.LatLng(that.latitude, that.longitude)
          }else{
           var center = new TMap.LatLng(that.latitude, that.longitude)
          }
          //map TMap.Map() 
          var map = new TMap.Map(document.getElementById('container'), {
              center: center,//
              zoom: 11,   //
              viewMode:'2D',
          });

          var markerLayer = new TMap.MultiMarker({
                      id: 'container',
                      map: map,
                      styles: { //
                                      "marker": new TMap.MarkerStyle({
                                          "width": 25,
                                          "height": 35,
                                          "anchor": { x: 16, y: 32 },
                                          "src": "https://mapapi.qq.com/web/lbs/javascriptGL/demo/img/markerDefault.png"
                                      })
                                  },
                      geometries: [{ //
                                      "id": "demo",
                                      "styleId": "marker",
                                      "position": new TMap.LatLng(that.latitude, that.longitude),
                                      "properties": {
                                      "title": "marker"
                                      }
                                  }],
                  });
          };
          function showErr() {
            console.log("")
          };
    },
    methods:{
      getUrlKey(name) {
      		return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(location.href) || [, ""])[1].replace(/\+/g, '%20')) || null
      	},
      exa_switch_one(){
           this.isBreak=true;
           this.value = 1
      },
      exa_switch_two(){
          this.isBreak=false;
          this.value = 2
      },
        appointment(){
          var that = this
          layui.use('layer', function() {
            var layer = layui.layer;
            if(that.value==1){
              if(that.address==''){
                layer.msg("")
              }
              else if(that.date==''){
                layer.msg("")
              }
              else if(that.phone==""){
                 layer.msg("")
              }else{
                that.$addr.post('/index/proxy/collect', {
                       type:that.value,
                       address:that.address,
                       latitude:that.latitude,
                       longitude:that.longitude,
                       date:that.date,
                       phone:that.phone
                     })
                     .then(function (response) {
                       console.log(response)
                       if(response.data.code==200){
                         layer.msg("")
                         that.text = ""
                          that.bled = true
                          that.run()
                       }

                     })
              }
            }else{
              if(that.date==''){
                layer.msg("")
              }
              else if(that.phone==""){
                 layer.msg("")
              }else{
                that.$addr.post('/index/proxy/collect', {
                       type:that.value,
                       date:that.date,
                       phone:that.phone
                     })
                     .then(function (response) {
                       console.log(response)
                       if(response.data.code==200){
                         layer.msg("")
                         that.text = ""
                        that.bled = true
                       }

                     })
              }
            }
          console.log(that.value)
        })
      },
      toMap() {
        if(this.brand_id == null) {
            this.brand_id = 0;
        }
       let url = escape(
         `${window.location.origin}${window.location.pathname}#/examination`
       )
       console.log(url)
       this.$router.go(-1)
        sessionStorage.setItem("nickname", this.nickname);
        sessionStorage.setItem("tel", this.tel);
        window.location.href="https://apis.map.qq.com/tools/locpicker?search=1&type=0&backurl="+url+"&key=W24BZ-WXDCO-JJGWX-SOQQZ-2HQRO-5JBJ4&referer="
    },
    //
    getLocation() {
        let location = this.$route.query.latng; //
        if(location != null) {
            let arr = location.split(",");
            this.latitude = arr[0]; //
            this.longitude = arr[1]; //
        }
        this.address = this.$route.query.addr; //
        this.city = this.$route.query.city; //
      },
     run(){    //
        this.$router.push({
          path: '/success',
          });
      }
   /*   removeMarker() {
                  if (markerLayer) {
                      markerLayer.setMap(null);
                      markerLayer = null;
                  }
              } */
     }
  }
</script>

<style scoped>
  @import '../assets/css/examination.css';

</style>



