<template>
  <!-- -->
  <div class="personal_center">
    <!--  -->
    <div class="personalcenter_top">
      <!--  -->
      <div class="personalcenter_top_login">
          <div class="personalcenter_top_loginimg">
            <img :src="avatar" >
            <div class="vip">
              <img src='../../static/img/VIP.png' >
            </div>
          </div>
          <div class="personalcenter_top_logintext">
            <p>{{nickname}}</p>
            <!--<p><span style="padding-left: 10px;">2020-02-02</span></p>-->
          </div>
          <div class="top_top_set">
            <a href="#"></a>
          </div>
      </div>
      <!--  -->
      <div class="personalcenter_bottom">
        <a href="#" @click="integral">
          <div class="personalcenter_bottom_item">
            <p>5</p>
            <p></p>
          </div>
        </a>
        <a href="#" @click="agency">
          <div class="personalcenter_bottom_item">
            <p>0</p>
            <p></p>
          </div>
        </a>
        <a href="#" @click="appointment">
          <div class="personalcenter_bottom_item">
            <p>0</p>
            <p></p>
          </div>
        </a>
        <a href="#" @click="coupon">
          <div class="personalcenter_bottom_item">
            <p><img src="../../static/img/personalcenter_coupon.png" ></p>
            <p></p>
          </div>
        </a>
      </div>
    </div>
    <!---->
    <div class="personalcenter_cont">
      <!--  -->
      <div class="personalcenter_cont_news">
        <div class="personalcenter_cont_scoll">
            <div><p class="dian"></p></div>
            <marquee>
             <p class="word">,</p>
            </marquee>
        </div>
      </div>
      <!--  -->
      <div class="personalcenter_cont_order">
        <!--  -->
        <div class="dingdan">
          <div class="dingdan_top">
            <a href="" @click="order">
              <p></p>
              <p><span></span><i class="iconfont icon-youjiantou"></i></p>
            </a>
          </div>
          <div class="dingdan_center">
              <div class="dingdan_center_a">
                <a href="#" @click="payment">
                  <img src="../../static/img/personal_center_pay.png" >
                  <p></p>
                </a>
                <i class="iconfont icon-youjiantou"></i>
               </div>
               <div class="dingdan_center_a">
                <a href="#" @click="delivered">
                  <img src="../../static/img/personal_center_delivered.png" >
                  <p></p>
                </a>
                <i class="iconfont icon-youjiantou"></i>
               </div>
               <div class="dingdan_center_a">
                <a href="#" @click="receive">
                  <img src="../../static/img/personal_center_receive.png" >
                  <p></p>
                </a>
                <i class="iconfont icon-youjiantou"></i>
               </div>
               <div class="dingdan_center_a">
                <a href="#" @click="evaluated">
                  <img src="../../static/img/personal_center_evaluated.png" >
                  <p></p>
                </a>
                <i class="iconfont icon-youjiantou"></i>
               </div>
               <div class="dingdan_center_a">
                <a href="#" @click="refund">
                  <img src="../../static/img/personal_center_refund.png" >
                  <p>/</p>
                </a>
                <i class="iconfont icon-youjiantou"></i>
               </div>
          </div>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'personal_center',
  data () {
    return {
      msg: 'personal_center',
      avatar: "../../static/img/portrait.jpg",
      nickname: ""
    }
  },
  mounted:function(){
        this.$store.state.heard_title =' - ';
        var token = localStorage.getItem("token");
        this.$addr.post('/index/index/userInfo',{token:token})
              .then(response => {
                this.avatar = response.data.result.user.avatar;
                this.nickname = response.data.result.user.nickname;
        } )
  },
  methods:{
    //
    integral:function(){
      this.$router.push({
         name: 'integral',
       })
    },
    //
    agency:function(){
      this.$router.push({
         name: 'news',
       })
    },
    //
    appointment:function(){
      this.$router.push({
         name: 'isdetails',
       })
    },
    //
    coupon:function(){
      this.$router.push({
         name: '',
       })
    },
    //
    order:function(){
      this.$router.push({
         path: 'order',
       })
    },
    //
    payment:function(){
      this.$router.push({
         name: 'order',
       })
    },
    //
    delivered:function(){
      this.$router.push({
         name: 'order',
       })
    },
    //
    receive:function(){
      this.$router.push({
         name: 'order',
       })
    },
    //
    evaluated:function(){
      this.$router.push({
         path: 'order',
       })
    },
    refund(){
      this.$router.push({
         path: 'after_sale',
       })
    },

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import '../assets/css/personal_center.css'

</style>



