<template>
  <!-- -->
  <div class="register">
      <!--  -->
      <div class="register_common">
        <p></p>
      	<input type="number" placeholder="" name="phone" v-model="phone_number">
      </div>
      <!--  -->
      <div class="register_common">
        <p></p>
        <div class="register_code">
          <input type="text" placeholder="" name="Verification" v-model="verification">
          <button @click="verification_code" :disabled="bled">{{text}}</button>
        </div>
      </div>
      <!--  -->
      <div class="register_common">
      	<p></p>
      	<div>
      		<input type="radio" value="1" name="sex" v-model="sex">
      		<input type="radio" value="2" name="sex" v-model="sex">
      	</div>
      </div>
      <!--  -->
      <div class="register_common">
      	<p></p>
      	<div class="register_address">
            <select name="province" v-model="province">
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
            </select>
            <select name="city" v-model="city">
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
            </select>
            <select name="area" v-model="area">
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
            </select>
      	</div>
      </div>
      <div class="register_common">
        <p></p>
          <input type="text" name="address" v-model="address" placeholder=""/>
      </div>
      <!--  -->
      <div class="register_common">
      	<p></p>
      	<div class="register_number">
      		<select name="plate" v-model="plate">
      				<option value=""></option>
      				<option value=""></option>
      				<option value=""></option>
      				<option value=""></option>
      		</select>
      		<input type="text" maxlength="6" placeholder="" v-model="car_number">
      	</div>
      </div>
      <!--  -->
      <div class="register_common">
        <p></p>
        <div class="register_distance">
          <input type="number" maxlength="4" placeholder="" name="distance" v-model="kilometre">/
        </div>
      </div>
      <!--  -->
      <div class="register_btn">
      	<!-- <a href="javascript:;"></a> -->
          <button @click="submit_register"></button>
      </div>
  </div>
</template>
<script>
  import Vue from 'vue'
  export default {
      name: 'Register',
        data(){
            return {
              phone_number:"",//
              verification:"",//
              sex:"1",
              province:"",
              city:'',
              area:'',
              address:'',
              plate:"",
              car_number:'',
              kilometre:"",
              bled:false,
              text:""
            }
        },
        mounted:function(){
          this.$store.state.heard_title =' - ';

          },
        methods: {
          //
          verification_code:function(){
            let that = this;
            //
              layui.use('layer', function(){
                let layer = layui.layer;
/*                let index = layer.load(1, {
              	shade: [0.1, '#fff'] //0.1
              }); */
                let reg1 = /^[1][3,4,5,7,8][0-9]{9}$/;
                 let phone = that.phone_number.trim()
                if(reg1.test(phone)){
                    that.bled = true
                    that.$addr.post('/index/register/input',{
                        telephone:that.phone_number
                      })
                           .then(res => {
                            if(res.data.code == 0){
                              layer.close( layer.load(1, {
                                  shade: [0.1, '#fff'] //0.1
                                }) );
                              layer.msg(res.data.result.msg);
                            }else{
                              layer.close( layer.load(1, {
                                shade: [0.1, '#fff'] //0.1
                              }));
                              layer.msg(res.data.result.msg);
                              var count = 60;
                              that.text = "("+count+")"
                              count = count - 1
                              var times = setInterval(function () {
                              	if (count == 0) {
                              		that.bled = false
                              		that.text=""
                              		count = 60;
                              		clearInterval(times)
                              	} else {
                              		 that.text = "("+count+")"
                              		count--;
                              	}
                              }, 1000)
                            }
                    } )
                 }else{
                   layer.msg("");
                 }
            });
          },
          //
            submit_register: function (event) {
              var that = this;
              layui.use('layer', function(){
                var layer = layui.layer;
              //
              if(that.phone_number==""){
                layer.msg('');
              }
              else if(that.phone_number.length!=11){
                 layer.msg('');
              }
              else if(that.verification==""){
                layer.msg('');
              }
              else if(that.car_number ==""){
                layer.msg('');
              }
              else if(that.car_number.length!=6){
                 layer.msg('');
              }
              else if(that.kilometre == ""){
                layer.msg('');
              }

              else{
                // 
                that.$addr.post('/index/register/index',{
                    mobile : that.phone_number,
                    yzm:that.verification,
                    gender: that.sex,
                    province: that.province ,
                    city: that.city,
                    district: that.area,
                    address: that.address,
                    number: that.plate + that.car_number,
                    km: that.kilometre
                })
                  .then(response => {
                    layui.use('layer', function(){
                      var layer = layui.layer;
                    if(response.data.code == 200){
                        layer.msg('')
                             that.$router.push({
                               name: 'index',
                      })
                    }else{
                       layer.msg(response.data.result.error)
                    }
                })
              })
              .catch(function (error) {
                console.log(error);
              });
            }
          });
        }
     }
  }
</script>

<style scoped>
  @import '../assets/css/Register.css'

</style>



